* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  display: block;
  align-content: center;
}

.flag {
  width: 100px;
}

.country_item {
  display: flex;
  align-items: center;
  margin: 20px;
  
}
.country_title {
    font-size: 20px;
  font-weight: 500;
}
.info-title-element {
  font-weight: bold;
}